<template>
     <div :class="{'modal': true, 'active': active}">
        <div v-if="shadow" @click="shadowClicked" 
            class="modal-overlay"
            :style="{opacity: shadowOpacity}"></div>
        <div :class="{'modal-container': true, 'modal-sizing-fill': fillSmallScreen}">
            <div class="modal-header" v-if="showHeader">
                <button @click="xButtonClicked" class="btn btn-clear float-right"></button>
                <slot name="header"></slot>
            </div>
            <div class="modal-body" v-if="showBody">
                <slot name="body"></slot>
                <slot></slot>
            </div>
            <div class="modal-footer" v-if="showFooter">
                <slot name="footer"></slot>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        active: { type: Boolean, 'default': false },
        shadow: { type: Boolean, 'default': true },
        shadowOpacity: { type: Number, 'default': 0.5 },
        xbutton: { type:Boolean, 'default': true },
        fillSmallScreen: { type:Boolean, 'default': false },
    },

    computed: {
        hasDefaultSlot() { return !!this.$slots.default; },
        showHeader() { return !!this.$slots['header'] || this.xbutton; },
        showBody() { return this.$slots['body'] || this.hasDefaultSlot; },
        showFooter() { return !!this.$slots['footer']; }
    },

    methods: {
        xButtonClicked() {
            this.$emit('xclick');
        },

        shadowClicked() {
            console.log('please');
            this.$emit('shadow-click');
        }
    }
};
</script>

<style lang="scss" scoped>
.modal-sizing-fill {
    min-width: 90%;
}

@media screen and (min-width: 400px ) {
    .modal-sizing-fill {
        min-width: 368px;
    }
}
</style>


