import {PCTInfo, MechanicInfo} from '../../misc/test-values';
import {getOriginalQueryProp, copyPropertyList, copyExistingPropertyList, isStringWithContent} from '../../misc/util';

export default {
    state: {
        showBrandingLogo: true,
        showBranding: true,

        logo: null,
        companyName: null,

        visPrincipalColor: '#40c057',
        visInterestColor: '#4c6ef5',
        visTaxesColor: '#fcc419',

        displayInitialPopover: true,
        initialPopoverHeaderText: null,
        initialPopoverBodyText: null,

        applyRedirectText: null,
        applyRedirect: null,
        disclaimer: null
    },

    getters: {
    },

    mutations: {
        getBrowserBrandingInfo(state) {
            state.showBrandingLogo = !getOriginalQueryProp('hbrandlogo', false);
            state.showBranding = !getOriginalQueryProp('hbrand', false);

            if (!isStringWithContent(state.applyRedirect)) {
                state.applyRedirect = '#';
            }
        },

        setBrandingInfo(state, brandingInfo) {
            copyExistingPropertyList(brandingInfo, state);
        }
    },

    actions: {
        afterInitialPopoverDisplay(context) {
            context.state.displayInitialPopover = false;
        },

        /**
         * Loads branding information for the current calculator.
         */
        fetchBrandingInfo(context) {
            // #TODO: for now this doesn't need to do anything.
        },

        navigateToApplication(context) {
            if (context.state.applyRedirect === '#') return;
            setTimeout(() => {
                if (top && top.window) {
                    top.window.location.href = context.state.applyRedirect;
                } else {
                    window.location.href = context.state.applyRedirect;
                }
            }, 200);
        }
    }
};


