export function fmtMoney(n) {
    return parseFloat(n.toFixed(2));
}

function getTermLengthAsMonths(termLength, termUnits) {
    switch (termUnits) {
        case 'years': return termLength * 12;
        case 'months':
        default: return termLength;
    }
}

export function calculateLoanPayment(presentValue, ratePerPeriod, numberOfPeriods) {
    if (ratePerPeriod > 0) {
        return (ratePerPeriod * presentValue) / (1 - 1/Math.pow(1 + ratePerPeriod, numberOfPeriods));
    } else {
        return presentValue / numberOfPeriods;
    }
}

export function calculateAutoLoan(data) {
    const termLengthInMonths = getTermLengthAsMonths(data.termLength, data.termUnits);
    const principal = data.loanAmount - data.downPayment;
    const monthlyPayment = calculateLoanPayment(principal, data.interestRate / 100 / 12, termLengthInMonths);
    const interestPayment = monthlyPayment * (data.interestRate / 100);
    const principalPayment = monthlyPayment - interestPayment;
    
    return {
        'total': monthlyPayment,
        'principal': principalPayment,
        'interest': interestPayment
    };
}

export function calculateMortgageLoan(data) {
    const termLengthInMonths = getTermLengthAsMonths(data.termLength, data.termUnits);
    const principal = data.loanAmount - data.downPayment;
    const monthlyPayment = calculateLoanPayment(principal, data.interestRate / 100 / 12, termLengthInMonths);
    const interestPayment = monthlyPayment * (data.interestRate / 100);
    const principalPayment = monthlyPayment - interestPayment;
    
    return {
        'total': monthlyPayment,
        'principal': principalPayment,
        'interest': interestPayment
    };
}

export function calculateNoop(data) {
    return {
        'total': 0,
        'principal': 0,
        'interest': 0
    };
}

export const calculators = {
    'auto-loan': calculateAutoLoan,
    'mortgage-loan': calculateMortgageLoan,
    'noop': calculateNoop
};

export function forceNumber(n) {
    let p = parseFloat(n)
    return isFinite(p) ? p : 0;
}


