import {initSocialMediaPlatforms} from './social';
import {saveOriginalWindowQuery} from './misc/util';
import {loadTestValues} from './misc/test-values';

saveOriginalWindowQuery();
loadTestValues();

import Vue from 'vue'
import App from './App'
import store from './store';

Vue.config.productionTip = false

initSocialMediaPlatforms();

/* eslint-disable no-new */
new Vue({
    el: '#app',
    store,
    render: h => h(App),

    created() {
    },

    methods: {
    }
});


