const MONEY_REMOVE_REGEX = /[,\-+\s]+/g;

export default {
    methods: {
        moneyAsNumber(val) {
            switch (typeof val) {
                case 'number' : return val;
                case 'string':
                    MONEY_REMOVE_REGEX.lastIndex = 0;
                    return parseFloat(val.replace(MONEY_REMOVE_REGEX, '')) || 0;
                default: return 0;
            }
        },

        // formatMoneyValue(val, addCommas) {
        //     if (typeof val !== 'number') {
        //         if (typeof val === 'string') {
        //             let strippedVal = this.parseMoneyValue(val);
        //             val = parseFloat(strippedVal) || 0;
        //         } else {
        //             val = 0;
        //         }
        //     }
        //     return val;
        // },
    }
};


