<template>
    <div class="legend-container">
        <div :key="item" v-for="item in chartData" class="legend-item">
            <div class="legend-circle" :style="{'background-color': item.color}">&nbsp;</div>
            <div class="legend-label">{{item.label}}</div>
            <div class="legend-sep"></div>
            <div class="legend-value">${{item.value.toFixed(2)}}</div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        chartData: { type: Array }
    },
};
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';

$legend-text-size: 1.2rem;

.legend-container {
    display: flex;
    flex-direction: column;
}

.legend-circle {
    min-width: 16px;
    width: 16px;
    height: 8px;
    background-color: transparent;
    border-radius: 4px;
}

.legend-item {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.legend-label {
    margin-left: 0.3rem;
    margin-right: 0.3rem;
    font-size: $legend-text-size;
}

.legend-sep {
    flex: 1;
    height: 1px;
    border-bottom: 1px dashed $border-color;
}

.legend-value {
    margin-left: 0.3rem;
    font-size: $legend-text-size;
}
</style>


