<template>
    <select @change="onSelected">
        <option v-if="nullOption" value="-1">{{nullOptionText}}</option>
        <option :key="val" v-for="(val, index) in mappedValues" :value="index">{{val.label}}</option>
    </select>
</template>

<script>
export default {
    props: {
        'nullOption': { type: Boolean, 'default': true },
        'nullOptionText': { type: String, 'default': '' },
        'useIndex': { type: Boolean, 'default': false },
        'values': { type: Array, 'default': () => [] }
    },

    computed: {
        mappedValues() {
            if (this.values) {
                return this.values.map((v) => {
                    if (typeof v === 'object') return v;
                    else return { value: v, label: v };
                })
            } else {
                return null;
            }
        }
    },

    methods: {
        onSelected($event) {
            let index = parseInt($event.target.value);
            if (!Number.isFinite(index)) { index = -1; }

            if (this.useIndex) {
                this.$emit('change', index);
            } else {
                if (index < 0) {
                    this.$emit('change', null);
                } else {
                    this.$emit('change', this.values[index]);
                }
            }
        }
    }
};
</script>

<style>
</style>



