<template>
    <div>
        <div :class="{'modal': true, 'active': applySuccessModalActive}">
            <div class="modal-overlay apply-modal-overlay"></div>
            <div class="modal-container apply-modal">
                <div class="modal-header">
                    <button @click="closeSuccessModal" class="btn btn-clear float-right"></button>
                    <div class="modal-title">Thank You!</div>
                </div>
                <div class="modal-body">
                    <div class="content">
                        <div>
                            Thank you for choosing to apply. We will now redirect you to:
                        </div>
                        <div>
                            <a :href="branding.applyRedirect" target="_top">
                                {{branding.applyRedirectText}}
                            </a>
                        </div>
                        <div class="muted-color am-extra-margin-top">
                            Click the link above if you are not redirected shortly.
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <form>
                        <button @click="closeSuccessModal" type="button" class="btn primary">Close</button>
                    </form>
                </div>
            </div>
        </div>
        <div :class="{'modal': true, 'active': formModalActive}">
            <div class="modal-overlay apply-modal-overlay"></div>
            <div class="modal-container apply-modal">
                <div class="modal-header">
                    <button @click="cancelApply" class="btn btn-clear float-right"></button>
                    <div class="modal-title">Apply Now</div>
                </div>
                <div class="modal-body">
                    <div class="content">
                        <form onsubmit="return false;">
                            <div class="form-group">
                                <label for="applyFirstNameInput" class="form-label">First Name</label>
                                <input
                                    v-model="firstName"
                                    id="applyFirstNameInput" 
                                    class="form-input" type="text" placeholder="First Name">
                                <div :style="{display: firstNameError ? 'block' : 'none'}"
                                    class="form-error">{{firstNameError}}</div>
                            </div>

                            <div class="form-group">
                                <label for="applyLastNameInput" class="form-label">Last Name</label>
                                <input
                                    v-model="lastName"
                                    id="applyLastNameInput" 
                                    class="form-input" type="text" placeholder="Last Name">
                                <div :style="{display: lastNameError ? 'block' : 'none'}"
                                    class="form-error">{{lastNameError}}</div>
                            </div>

                            <div class="form-group">
                                <label for="applyEmailInput" class="form-label">Email Address</label>
                                <input 
                                    v-model="emailAddress"
                                    id="applyEmailInput" 
                                    class="form-input" type="text" placeholder="Email Address">
                                <div :style="{display: emailAddressError ? 'block' : 'none'}"
                                    class="form-error">{{emailAddressError}}</div>
                            </div>

                            <div class="form-group flex-layout flex-center">
                                <small class="muted-text">AND/OR</small>
                            </div>

                            <div class="form-group">
                                <label for="applyPhoneInput" class="form-label">Phone Number </label>
                                <input 
                                    v-model="phoneNumber"
                                    id="applyPhoneInput" 
                                    class="form-input" type="text" placeholder="Phone Number">
                                <div :style="{display: phoneNumberError ? 'block' : 'none'}"
                                    class="form-error">{{phoneNumberError}}</div>
                            </div>
                        </form>
                    </div>
                </div>
                <div class="modal-footer">
                    <form>
                        <div class="form-group flex-layout flex-column">
                            <!-- <div @click="fillOutWithGoogle" class="google-fast-fill">
                                <img class="fast-fill-icon" src="/scalc/static/img/FB-f-Logo__white_29.png">
                                <div class="fast-fill-text">Fill With Google</div>
                                <spinner v-if="loadingGoogleInfo" :thickness="8" :size="24" ></spinner>
                            </div> -->

                            <div @click="fillOutWithFacebook" class="facebook-fast-fill">
                                <img class="fast-fill-icon" src="/scalc/static/img/FB-f-Logo__white_29.png">
                                <div class="fast-fill-text">Fill With Facebook</div>
                                <div class="flex-1">&nbsp;</div>
                                <spinner v-if="loadingFacebookInfo" :thickness="8" :size="24" ></spinner>
                            </div>
                        </div>
                        <div class="form-group">
                            <button @click="confirmApply" type="button" class="btn primary">Apply</button>
                            <button @click="cancelApply" type="button" class="btn">Cancel</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { delay, parseFullName } from '../misc/util';
import { mapState, mapGetters, mapActions } from 'vuex';
import { facebookLogin, facebookGetUserInfo, googleLogin } from '../social';
import spinner from './spinner';

const MODAL_DISAPPEAR_DELAY = 100;

const EMAIL_REGEX = /.*@.*/;
const PHONE_REGEX = /^(.*\d.*){10,}$/;

export default {
    props: {show: {type: Boolean, 'default': false}},

    data: () => ({
        firstName: '',
        lastName: '',
        phoneNumber: '',
        emailAddress: '',

        firstNameError: null,
        lastNameError: null,
        emailAddressError: null,
        phoneNumberError: null,

        formModalActive: false,
        applySuccessModalActive: false,
        modalTransitioning: false,

        loadingFacebookInfo: false,
        loadingGoogleInfo: false
    }),

    watch: {
        formModalActive(newValue) {
            if (!this.modalTransitioning && !this.applySuccessModalActive) {
                this.$emit('closed');
            }
        },

        applySuccessModalActive(newValue) {
            if (!this.modalTransitioning && !this.formModalActive) {
                this.$emit('closed');
            }
        },

        async show(newValue) {
            if (newValue && !this.someModalActive) {
                if (this.applySuccessModalActive) {
                    this.applySuccessModalActive = false;
                    this.modalTransitioning = true;
                    await delay(MODAL_DISAPPEAR_DELAY);
                    this.modalTransitioning = false;
                }
                this.formModalActive = true;
            }
        }
    },

    computed: {
        someModalActive() {
            return this.formModalActive || this.applySuccessModalActive || this.modalTransitioning;
        },
        ...mapState(['branding', 'calculator'])
    },

    methods: {
        async onApplySuccess() {
            if (this.formModalActive) {
                this.formModalActive = false;
                this.modalTransitioning = true;
                await delay(MODAL_DISAPPEAR_DELAY);
                this.modalTransitioning = false;
            }
            this.applySuccessModalActive = true;
        },

        async confirmApply($event) {
            $event.preventDefault();
            if (this.checkFields()) {
                const lead = {
                    firstName: this.firstName,
                    lastName: this.lastName,
                    phoneNumber: this.phoneNumber,
                    emailAddress: this.emailAddress
                };
                
                this.$store.dispatch('sendApplyInfo', {lead});
                // I give the UI time to update and do this on the next tick.
                await delay(0);
                await this.onApplySuccess();
            }
        },

        async fillOutWithGoogle() {
            this.loadingGoogleInfo = true;
            try {
                await googleLogin();
            } catch(e) {
                alert("There was an unknown error logging you into Google.");
                console.error('Error while getting google user info: ', e);
            }
            this.loadingGoogleInfo = false;
        },

        async fillOutWithFacebook() {
            this.loadingFacebookInfo = true;
            try {
                const response = await facebookLogin();
                const userInfo = await facebookGetUserInfo();

                if (!userInfo.firstName && !userInfo.lastName) {
                    const {first, last} = parseFullName(userInfo.name);
                    this.firstName = first;
                    this.lastName = last;
                } else {
                    this.firstName = userInfo.firstName || '';
                    this.lastName = userInfo.lastName || '';
                }
                this.emailAddress = userInfo.email || '';
            } catch(e) {
                alert("There was an unknown error logging you into Facebook.");
                console.error('Error while getting facebook user info: ', e);
            }
            this.loadingFacebookInfo = false;
        },

        closeSuccessModal() {
            this.applySuccessModalActive = false;
        },

        cancelApply() {
            this.formModalActive = false;
        },

        isValidEmail(email) {
            EMAIL_REGEX.lastIndex = 0;
            return !!EMAIL_REGEX.exec(email);
        },

        isValidPhone(phone) {
            PHONE_REGEX.lastIndex = 0;
            return !!PHONE_REGEX.exec(phone);
        },

        checkFields() {
            this.firstNameError = null;
            this.lastNameError = null;
            this.emailAddressError = null;
            this.phoneNumberError = null;

            if (!this.firstName || this.firstName.trim().length <= 0) {
                this.firstNameError = 'First name is required.';
            }

            if (!this.lastName || this.lastName.trim().length <= 0) {
                this.lastNameError = 'Last name is required.';
            }

            let emailOrPhone = false;
            if (this.emailAddress && this.emailAddress.trim().length > 0) {
                if (!this.isValidEmail(this.emailAddress)) {
                    this.emailAddressError = 'Invalid email address.';
                } else {
                    emailOrPhone |= true;
                }
            }

            if (this.phoneNumber && this.phoneNumber.trim().length > 0) {
                if (!this.isValidPhone(this.phoneNumber)) {
                    this.phoneNumberError = 'Invalid phone number.';
                } else {
                    emailOrPhone |= true;
                }
            }

            console.log(this.phoneNumberError, this.emailAddressError, emailOrPhone);
            if (!this.phoneNumberError && !this.emailAddressError && !emailOrPhone) {
                this.phoneNumberError = "Email address or phone number required.";
                this.emailAddressError = "Email address or phone number required.";
            }

            return !(this.firstNameError || this.lastNameError || this.emailAddressError || this.phoneNumberError);
        }
    },

    components: {
        spinner
    }
};
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';
@import '~@/assets/sass/open-color';

.apply-modal {
    min-width: 90%;
}

@media screen and (min-width: 400px ) {
    .apply-modal {
        min-width: 368px;
    }
}

.am-extra-margin-top {
    margin-top: $layout-padding / 2;
}

.apply-modal-overlay {
    opacity: 0 !important;
}

$facebook-fast-fill-bg: $oc-gray-8;
$google-fast-fill-bg: $oc-gray-5;

@mixin fast-fill-mixin {
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 2px;
    padding: px2rem(5) px2rem(8);
    cursor: pointer;
    margin-bottom: 8px;
}

.google-fast-fill {
    background-color: $google-fast-fill-bg;
    @include fast-fill-mixin;

    &:hover { background-color: lighten($google-fast-fill-bg, 5%); }
    &:active { background-color: darken($google-fast-fill-bg, 5%); }
}

.facebook-fast-fill {
    background-color: $facebook-fast-fill-bg;
    @include fast-fill-mixin;

    &:hover { background-color: lighten($facebook-fast-fill-bg, 5%); }
    &:active { background-color: darken($facebook-fast-fill-bg, 5%); }
}

.facebook-fast-fill,
.google-fast-fill {
    .fast-fill-icon {
        width: 24px;
        height: 24px;
        margin-right: 8px;
    }

    .fast-fill-text {
        color: $text-on-primary-color;
    }
}
</style>


