export class Debouncer {
    /**
     * @param {number} debounceDur Duration between actions.
     * @param {function} defaultFn Default function called from debounce if one is not provided.
     * @param {any} _this Will be used as 'this' for the debounced function call.
     */
    constructor(debounceDur, defaultFn, _this) {
        this.debounceDur = debounceDur;
        this.defaultFn = defaultFn;
        this.pendingFn = null;
        this.currentTimeout = null;
        this._this = _this;
        this.__boundCallPendingFn = this.callPendingFn.bind(this);
    }

    debounce(dur, fn) {
        if (typeof dur === 'function') { fn = dur; dur = this.debounceDur; }
        if (dur === undefined) { dur = this.debounceDur; }
        if (fn === undefined) { fn = this.defaultFn; }
        this.pendingFn = fn;

        if (this.currentTimeout === null) {
            this.currentTimeout = setTimeout(this.__boundCallPendingFn, dur);
        }
    }

    callPendingFn() {
        if (this.pendingFn) this.pendingFn.call(this._this);
        this.currentTimeout = null;
    }
}


