<template>
    <div id="app">
        <calculator v-if="!loadingCalculator || renderCalculator" @apply="onCalculatorApply"></calculator>
        <apply-modal @closed="onApplyModalClosed" :show="showApplyModal"></apply-modal>
    </div>
</template>

<script>
import Calculator from './components/Calculator';
import ApplyModal from './components/ApplyModal';
import { getCurrentCalculatorInfo } from './misc/calc-api';
import Vue from 'vue';

export default {
    name: 'app',

    data: () => ({
        showApplyModal: false,
        renderCalculator: true,

        // #TODO: Add a spinner for this in here.
        loadingCalculator: false,

        reloadCalculatorTimeout: null,
        windowMessagesListener: null,
    }),

    components: {
        Calculator,
        ApplyModal
    },

    async created() {
        this.loadingCalculator = true;
        let calcInfo = await getCurrentCalculatorInfo();
        if (calcInfo) {
            this.$store.commit('setBrandingInfo', calcInfo['branding']);
            this.$store.commit('getBrowserBrandingInfo', calcInfo['calculator']);
            this.$store.commit('setCalculatorInfo', calcInfo['calculator']);
            console.log('loaded calculator information');
            Vue.nextTick(() => this.loadingCalculator = false);
        } else {
            // #TODO: show an alert or something here.
            console.warn('No default calculator information loaded.');
        }

        if ((calcInfo && calcInfo['preview']) || process.env.NODE_ENV === 'development') {
            console.log('In preview mode.');
            this.createWindowMessagesListener();
        }
    },

    destroyed() {
        this.clearWindowMessagesListener();
    },
    
    methods: {
        createWindowMessagesListener() {
            this.windowMessagesListener = (event) => {
                console.log('received message: ', event.data);
                if (event.data && event.data['messageType']) {
                    const messageType = event.data['messageType'];
                    this.processWindowMessage(event, messageType, event.data);
                }
            };
            window.addEventListener('message', this.windowMessagesListener, false);
        },

        clearWindowMessagesListener() {
            if (this.windowMessagesListener) {
                window.removeEventListener('message', this.windowMessagesListener);
                this.windowMessagesListener = null;
            }
        },

        processWindowMessage(event, type, data) {
            switch (type) {
                case 'previewUpdate':
                    const previewData = data['previewData'];
                    this.commitPreviewData(previewData['branding'], previewData['calculator']);
                    break;
                case 'connect':
                    event.source.postMessage({
                        messageType: 'connected',
                        time: Date.now()
                    }, event.origin);
                    break;
                case 'refresh':
                    this.reloadCalculator();
                    break;
            }
        },

        commitPreviewData(branding, calculator) {
            this.$store.commit('setBrandingInfo', branding);
            this.$store.commit('setCalculatorInfo', calculator);
            console.log('Committed preview data.');
        },

        reloadCalculator() {
            this.renderCalculator = false;

            if (this.reloadCalculatorTimeout !== null) {
                clearTimeout(this.reloadCalculatorTimeout);
                this.reloadCalculatorTimeout = null;
            }

            this.reloadCalculatorTimeout = setTimeout(() => {
                this.renderCalculator = true;
                this.reloadCalculatorTimeout = null;
            }, 1);
        },

        onCalculatorApply() {
            this.showApplyModal = true;
        },

        onApplyModalClosed() {
            this.showApplyModal = false;
        }
    }
}
</script>

<style src="./assets/sass/core.scss" lang="scss"></style>
<style lang="scss">
#app {
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding: 0;
    margin: 0;
}
</style>



