import * as calc from '../../misc/calculations';
import {forceNumber} from '../../misc/calculations';
import {PCTInfo, MechanicInfo} from '../../misc/test-values';
import {getOriginalQueryProp, copyPropertyList, copyExistingPropertyList} from '../../misc/util';
import {reportEngagement, reportLead} from '../../misc/calc-api';

/**
 * This module just contains some metadata concerning calculators.
 * The metadata is ideally information provided by the server
 * that was configured by the client that owns the current calculator.
 */
export default {
    state: {
        defaultLoanAmount: 20000,
        minLoanAmount: 100.00,
        maxLoanAmount: 300000.00,

        minDownPayment: 0.00,
        defaultDownPayment: 3000,

        // #TODO: enforce minimum and maximum term length.
        minTermLength: 1, 
        defaultTermLength: 36,
        maxTermLength: 0xffffffff,

        // #TODO: enforce minimum and maximum interest rate.
        minInterestRate: 0.00,
        defaultInterestRate: 2.35,
        maxInterestRate: 100.00,

        calculatorType: 'auto-loan',
        visPrincipalVisible: true,
        visInterestVisible: true,

        monthlyPayment: 0,
        monthlyPaymentPrincipal: 0,
        monthlyPaymentInterest: 0,

        loanAmountName: 'Vehicle Loan Amount',
        downPaymentName: 'Down Payment',
        interestRateName: 'Interest Rate',

        sendingEvents: false,
        storedCalcInfo: null,
        staleEngagementData: false,

        termInterests: []
    },

    getters: {
        monthlyPaymentForcedNumber: (state) => forceNumber(state.monthlyPayment),
        typeDisplayText: (state) => {
            switch (state.calculatorType) {
                case 'auto-loan': return 'AutoCalc';
                case 'mortgage-loan': return 'MortgageCalc';
                case 'personal-loan': return 'PersonalCalc';
                default: return 'FinCalc';
            }
        }
    },

    mutations: {
        setCalculatorInfo(state, payload) {
            copyExistingPropertyList(payload, state);

            // Initial check for calculatro type, makes testing a lot easier
            // to have this here instead of encountering the error later while
            // doing calculations. #TODO: maybe I should remove this at some point.
            const calculatorFn = calc.calculators[state.calculatorType];
            if (!calculatorFn) {
                console.warn('No calculator for type: ' + state.calculatorType);
                console.warn('Using default type auto-loan');
                state.calculatorType = 'auto-loan';
            }
        },

        refreshEngagement(state) {
            state.staleEngagementData = false;
        },

        invalidateEngagement(state) {
            state.staleEngagementData = true;
        },

        setSendingEvents(state, payload) {
            // console.log('sending events = ', payload);
            state.sendingEvents = !!payload;
        },

        setMonthlyPaymentInfo(state, payload) {
            state.monthlyPayment = payload.total;
            state.monthlyPaymentPrincipal = payload.principal;
            state.monthlyPaymentInterest = payload.interest;
        },

        saveLastCalcInfo(state, payload) {
            state.storedCalcInfo = payload;
        }
    },

    actions: {
        fetchCalculatorInfo(context, payload) {
            // #TODO: for now this doesn't need to do anything.
        },

        /**
         * Calculates the monthly payment.
         */
        calculateMonthlyPayment(context, payload) {
            const calculatorFn = calc.calculators[context.state.calculatorType];
            if (!calculatorFn) {
                throw new Error('No calculator for type: ' + context.state.calculatorType);
            }

            const result = calculatorFn(payload)
            context.commit('setMonthlyPaymentInfo', result);

            context.commit('saveLastCalcInfo', {
                input: payload,
                output: result
            });

            // console.log('[engage] sending events: ', context.state.sendingEvents);
            if (context.state.sendingEvents && !context.state.staleEngagementData) {
                context.commit('invalidateEngagement');
                reportEngagement({
                    input: payload,
                    output: result
                }).catch((e) => console.warn('err-bad-eng', e));
            }
        },

        sendApplyInfo(context, payload) {
            // console.log('[apply] sending events: ', context.state.sendingEvents);
            if (context.state.sendingEvents) {
                payload.lead.loanType = context.state.calculatorType;
                const fullPayload = Object.assign(payload, context.state.storedCalcInfo);
                reportLead(fullPayload).then(() => {
                    context.dispatch('navigateToApplication');
                }, (e) => console.warn('err-bad-lead', e));
            }
        }
    }
};


