import {getOriginalQueryProp} from './util';

export const PCTInfo = {
    branding: {
        logo: '/scalc/static/img/pct-credit-union.png',
        companyName: 'PCT Federal Credit Union',
        initialPopoverHeaderText: 'Looking for a new ride or want to refinance your existing one?',
        initialPopoverBodyText: "Change the parameters in the calculator to get payment estimates based on PCT Federal Credit Union's current rates. Ready to buy? Click APPLY NOW to fill out our online loan app!",
        applyRedirectText: "PCT Federal Credit Union's Loan Application",
        applyRedirect: 'http://www.pctfcu.org/borrow/',
        disclaimer: "*APR = Annual Percentage Rate. Annual percentage rates, terms and availability may differ based upon evaluation of credit. Rates are subject to change and are based on a 0.25% protection package discount requiring one of the following: subscription to Credit Disability insurance, Auto Pay/Direct Pay/Payroll Allocation or opening of a new Neighbors checking account. Additional loan rates and terms may be available. Other restrictions may apply The figures shown are hypothetical and may not be applicable to your individual situation.",

        visPrincipalColor: '#40c057',
        visInterestColor: '#4c6ef5',
        visTaxesColor: '#fcc419'
    },

    calculator: {
        termInterests: [
            {len: 36, rate: 2.35},
            {len: 48, rate: 2.60},
            {len: 60, rate: 2.60},
            {len: 72, rate: 3.35},
            {len: 84, rate: 3.35}
        ]
    }
}

export const MechanicInfo = {
    branding: {
        logo: '/scalc/static/img/mechanics-coop-logo.png',
        companyName: 'Mechanics Cooperative Bank',
        initialPopoverHeaderText: 'Looking for a new ride or want to refinance your existing one?',
        initialPopoverBodyText: "Change the parameters in the calculator to get payment estimates based on Mechanic Cooperative Bank's current rates. Ready to buy? Click APPLY NOW to fill out our online loan app!",
        applyRedirectText: "Mechanic Cooperative Bank's Loan Application",
        applyRedirect: 'https://mechanics-coop.com/consumer-loan-application/',
        disclaimer: "*APR = Annual Percentage Rate. Annual percentage rates, terms and availability may differ based upon evaluation of credit. Rates are subject to change and are based on a 0.25% protection package discount requiring one of the following: subscription to Credit Disability insurance, Auto Pay/Direct Pay/Payroll Allocation or opening of a new Neighbors checking account. Additional loan rates and terms may be available. Other restrictions may apply The figures shown are hypothetical and may not be applicable to your individual situation.",

        visPrincipalColor: '#4dadf7',
        visInterestColor: '#fa5252',
        visTaxesColor: '#fcc419'
    },

    calculator: {
        termInterests: [
            {len: 36, rate: 3.99},
            {len: 48, rate: 4.50},
            {len: 60, rate: 4.75},
        ]
    }
}

export function loadTestValues() {
    const company = getOriginalQueryProp('company', 'pct');
    const info = company === 'mechanics' ? MechanicInfo : PCTInfo;
    window.__brandingInfo = info.branding;
    window.__calculatorInfo = info.calculator;

    // console.log('fast branding info: ', __brandingInfo);
    // console.log('fast calculator info: ', __calculatorInfo);
}


