function toQueryString(queryObject) {
	let ret = '';
	if (queryObject) {
		let first = true;
		for(let key in queryObject) {
			if (first) first = false;
			else ret += '&';
			ret += encodeURIComponent(key) + '=' + encodeURIComponent(queryObject[key]);
		}
	}
	return ret;
}

function addQueryToURL(url, queryObject) {
	let queryStart = url.indexOf('?');
	let hashStart = url.indexOf('#');

	if (hashStart < 0) hashStart = url.length;
	if (queryStart < 0) queryStart = hashStart;

	const endpoint = url.substr(0, queryStart);
	let query = url.substr(queryStart, hashStart - queryStart);
	const hash = url.substr(hashStart);
	const addon = toQueryString(queryObject);

	if (addon) {
		if (query[0] != '?') query = '?' + query;
		if (query.length > 1 && !query.endswith('&')) query += '&' + addon;
		else query += addon;
	}
	return endpoint + query + hash;
}

function processorJson(request) { return JSON.parse(request.responseText); }
function processorText(request) { return request.responseText; }

class BaseRequest {
	constructor(method, url) {
		this._method = method;
		this._data = undefined;
		this._async = true;
		this._url = url;
		this._query = undefined;
		this._type = 'application/x-www-form-urlencoded; charset=UTF-8';
		this._beforeSend = null;
		this._errorHandler = null;
	}

	query(q) { this._query = q; return this; }
	data(d) { this._data = d; return this; }
	url(u) { this._url = u; return this; }
	sync() { this._async = false; return this; }
	async() { this._async = true; return this; }
	contentType(type) { this._type = type; return this; }
	beforeSend(fn) { this._beforeSend = fn; return this; }
	errorHandler(fn) { this._errorHandler = fn; return this; }

	send(processor) {
		processor = processor || processorText;
		return new Promise((resolve, reject) => {
			const realUrl = addQueryToURL(this._url, this._query);
			const request = new XMLHttpRequest;
			request.open(this._method, realUrl, this._async);
			request.setRequestHeader('Content-Type', this._type);
			if (this._beforeSend) this._beforeSend(request);

			request.onload = () => {
				try {
					resolve(processor(request));
				} catch(err) {
					if (this._errorHandler) this._errorHandler(err, resolve, reject);
					else reject(err);
				}
			};

			request.onerror = (err) => {
				if (this._errorHandler) this._errorHandler(err, resolve, reject);
				else reject(err);
			};

			if (this._data) request.send(toQueryString(this._data));
			else request.send();
		});
	}

	json() {
		return this.send(processorJson);
	}

	then() {
		return Promise.prototype.then.apply(this.send(), arguments);
	}

	catch() {
		return Promise.prototype.catch.apply(this.send(), arguments);
	}
}

class HttpApiHelper {
	constructor(baseUrl, options) {
		if (baseUrl.endsWith('/')) {
			this.baseUrl = baseUrl.substr(0, baseUrl.length - 1);
		} else {
			this.baseUrl = baseUrl;
		}

		if (options) {
			this.beforeSend = options.beforeSend;
			this.errorHandler = options.errorHandler;
		}
	}

	get(url) { return this.createRequest('GET', url); }
	post(url) { return this.createRequest('POST', url); }
	put(url) { return this.createRequest('PUT', url); }
	del(url) { return this.createRequest('DELETE', url); }

	createRequest(method, url) {
		return new BaseRequest(method,  this.createUrl(url))
			.beforeSend(this.beforeSend)
			.errorHandler(this.errorHandler);
	}

	createUrl(endpoint) {
		if(endpoint.startsWith('/')) return this.baseUrl + endpoint;
		else return this.baseUrl + '/' + endpoint;
	}
}

const http = {
	get: (url) => new BaseRequest('GET', url),
	post: (url) => new BaseRequest('POST', url),
	put: (url) => new BaseRequest('PUT', url),
	del: (url) => new BaseRequest('DELETE', url),
	api: (baseUrl) => new HttpApiHelper(baseUrl)
};
export default http;


