<template>
    <div class="calc-container">
        <div v-if="branding.showBranding" class="branding">
            <div class="flex-layout flex-row">
                <div v-if="calculatorBackURL && calculatorBackURL.trim().length > 0">
                    <button @click="navigateBackToList" class="btn btn-sm btn-muted">
                        <!-- Left Chevron Icon -->
                        <svg class="v-icon" xmlns="http://www.w3.org/2000/svg"
                            width="24" height="24" viewBox="0 0 24 24"
                            fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round"
                            stroke-linejoin="round"><polyline points="15 18 9 12 15 6"></polyline>
                        </svg>
                        Back To List
                    </button>
                </div>
                <div class="flex-1">&nbsp;</div>
                <div class="branding-badge">{{typeDisplayText}}</div>
            </div>

            <!-- TODO: Fix centering & stretching -->
            <div v-if="branding.showBrandingLogo" class="branding-logo-container">
                <img :alt="branding.companyName" :src="branding.logo" class="branding-logo">
            </div>
        </div>
        <div class="container">
            <div :class="{row: true, 'flex-center': !hasCalculation}">
                <!-- Calculator Fields Container -->
                <div class="col col-6 col-sm-12">
                    <form class="calc-form" onsubmit="return false;">
                        <div class="form-group">
                            <div class="flex-layout extra-margin-bottom">
                                <label class="form-label">{{calculator.loanAmountName}}</label>
                                <div class="flex-1">&nbsp;</div>
                                <div class="form-line">
                                    <div for="loanAmountInput" class="form-static no-border-right">$</div>
                                    <input 
                                        id="loanAmountInput"
                                        :value="loanAmount"
                                        @change="setMoneyFilteredVal('loanAmount', $event.target.value)"
                                        class="form-input form-input-fheight no-border-left" 
                                        type="text">
                                </div>
                            </div>
                            <div class="flex-layout">
                                <div class="flex-1">
                                    <vue-slider
                                        :min="calculator.minLoanAmount"
                                        :max="calculator.maxLoanAmount"
                                        :interval="loanAmountInterval"
                                        :value="moneyAsNumber(loanAmount)"
                                        @input="setMoneyFilteredVal('loanAmount', $event)"
                                        class="slider" tooltip="never">
                                    </vue-slider>
                                </div>
                            </div>
                        </div>

                        <div class="form-group">
                            <div class="flex-layout extra-margin-botton">
                                <label class="form-label">{{calculator.downPaymentName}}</label>
                                <div class="flex-1">&nbsp;</div>
                                <div class="form-line">
                                    <div class="form-static no-border-right">$</div>
                                    <input v-model.lazy="downPayment" class="form-input flex-2 form-input-fheight no-border-left" type="text">
                                    <div class="form-static muted-color flex-1">{{downPaymentPercentage}}</div>
                                </div>
                            </div>
                            <div class="flex-layout">
                                <div class="flex-1">
                                    <vue-slider
                                        :min="calculator.minDownPayment"
                                        :max="maxDownPayment"
                                        :interval="downPaymentInterval"
                                        :value="moneyAsNumber(downPayment)"
                                        @input="setMoneyFilteredVal('downPayment', $event)"
                                        class="slider" tooltip="never">
                                    </vue-slider>
                                </div>
                            </div>
                        </div>

                        <div class="form-group">
                            <label class="form-label">Loan Term <span v-if="constantLoanTermUnits" class="muted-color">({{constantLoanTermUnitsText}})</span></label>
                            <div class="flex-layout form-line">
                                <div class="flex-layout form-line" style="margin-right: 8px;">
                                    <div class="flex-layout form-line">
                                        <input v-model.number="loanTerm" class="form-input">
                                    </div>
                                </div>
                                <div class="flex-layout form-line">
                                    <button
                                        :key="term"
                                        v-for="term of buttonTermInterests"
                                        @click.prevent="setProvidedLoanTerm(term.len, term.rate)"
                                        :class = "{'selected': loanTerm == term.len}"
                                        tabindex="-1" 
                                        type="button" 
                                        class="btn plain">{{term.len}}</button>
                                    <dropdown 
                                        tabindex="-1"
                                        null-option-text="..."
                                        :use-index="true"
                                        @change="onDropdownLoanTermSelected"
                                        v-if="dropdownTermInterests && dropdownTermInterests.length" 
                                        :values="dropdownTermInterests"
                                        class="form-select"></dropdown>
                                </div>
                            </div>
                        </div>

                        <div class="form-group">
                            <div class="flex-layout extra-margin-botton">
                                <label class="form-label">{{calculator.interestRateName}}</label>
                                <div class="flex-1">&nbsp;</div>
                                <div class="flex-layout form-line">
                                    <input v-model.number="interestRate" class="form-input">
                                    <div class="form-static">%</div>
                                </div>
                            </div>
                            <div class="flex-layout" v-if="false">
                                <div class="flex-1">
                                    <vue-slider
                                        :min="calculator.minInterestRate"
                                        :max="calculator.maxInterestRate"
                                        :interval=".01"
                                        :value="moneyAsNumber(interestRate)"
                                        @input="setMoneyFilteredVal('interestRate', $event)"
                                        class="slider" tooltip="never">
                                    </vue-slider>
                                </div>
                            </div>
                        </div>

                        <div class="form-group">
                            <div class="flex-layout flex-align-right">
                                <button :disabled="disableCalcButton" class="btn btn-primary" @click="showCalculation">
                                    Calculate
                                </button>
                            </div>
                        </div>
                    </form>
                </div>

                <!-- Calculator Results Container -->
                <div class="col col-6 col-sm-12 sm-no-top-pad"
                    :class="{'cresults-visible': hasCalculation, 'cresults-hidden': !hasCalculation}">
                    <div class="container" style="position: relative;">
                        <div class="calculations-loading flex-layout flex-column flex-center flex-center-cross"
                            :class="{'invisible-fadeout': resultsAnimationState == 1, 'visibile-fadein': resultsAnimationState == 0, 'noopacity': resultsAnimationState !== 0}">
                            <div style="margin-bottom: 16px;">Calculating...</div>
                            <bouncing-balls></bouncing-balls>
                        </div>
                        <div class="row flex-center"
                            :class="{'invisible-fadeout': resultsAnimationState == 3, 'visibile-fadein': resultsAnimationState == 2, 'noopacity': resultsAnimationState !== 2}">
                            <div class="col col-7 col-sm-12 calc-donut-chart-container">
                                <donut-chart 
                                    :mainText="'$' + monthlyPaymentForcedNumber.toFixed(2)"
                                    :subText="'/month'"
                                    :chartData="chartData"></donut-chart>
                            </div>
                            <div class="col col-5 no-h-pad col-sm-12 sm-no-top-pad">
                                <div class="container extra-padding-bottom">
                                    <div class="row no-h-pad">
                                        <div class="col-0 no-h-pad col-sm-2">&nbsp;</div>
                                        <div class="col-12 no-h-pad col-sm-8">
                                            <chart-legend :chartData="chartData"></chart-legend>
                                        </div>
                                        <div class="col-0 no-h-pad col-sm-2">&nbsp;</div>
                                    </div>
                                </div>
                                <div class="container flex-layout flex-column extra-padding-top no-h-pad">
                                    <button @click="applyForLoan" type="button" class="btn primary btn-large apply-now-btn">Apply Now!</button>
                                    <div class="flex-layout flex-row flex-wrap flex-center">
                                        <button  @click="showDisclaimer = true" type="button" class="btn btn-small extra-margin-top">Disclaimer</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <modal 
            :active="branding.displayInitialPopover" 
            :fill-small-screen="true" 
            @xclick="branding.displayInitialPopover=false" 
            :shadowOpacity="0"
            @shadow-click="branding.displayInitialPopover=false">
            <div class="modal-title" slot="header">{{branding.initialPopoverHeaderText}}</div>
            <div class="content" slot="body">
                {{branding.initialPopoverBodyText}}
            </div>
            <form slot="footer">
                <button type="button" class="btn primary" @click="afterInitialPopoverDisplay">Close</button>
            </form>
        </modal>

        <modal 
            :active="showDisclaimer" 
            :fill-small-screen="true" 
            @xclick="showDisclaimer = false"
            @shadow-click="showDisclaimer = false"
            :shadowOpacity="0">
            <div class="modal-title" slot="header">Disclaimer</div>
            <div class="content" slot="body">
                {{branding.disclaimer}}
            </div>
            <form slot="footer">
                <button @click="showDisclaimer = false" type="button" class="btn primary">Close</button>
            </form>
        </modal>
    </div>
</template>

<script>
import vueSlider from 'vue-slider-component';
import donutChart from './DonutChart';
import chartLegend from './ChartLegend';
import modal from './modal';
import BouncingBalls from './bouncing-balls';
import dropdown from './dropdown';
import MoneyMixin from '../mixins/money-mixin';
import {forceNumber} from '../misc/calculations';
import {Debouncer} from '../misc/debounce';
import {delay, getOriginalQueryProp} from '../misc/util';
import { mapState, mapGetters, mapActions } from 'vuex';

const MAX_LOAN_TERMS_ON_LINE = 4;

export default {
    name: 'Calculator',
    
    mixins: [MoneyMixin],

    data: () => ({
        loanAmount: 0,
        downPayment: 0,
        loanTerm: 0,
        loanTermUnits: 'months',
        interestRate: 0,
        calculationsVisible: false,
        chartData: [],
        listenToChanges: false,
        hasCalculation: false,
        disableCalcButton: false,

        /**
         * 0 - show loading
         * 1 - hide loading
         * 2 - show results
         * 3 - hide results
         */
        resultsAnimationState: 0,

        changes: 0,

        chartDataUpdateDebouncer: new Debouncer(100),
        showDisclaimer: false
    }),

    watch: {
        loanAmount() { this.changes++; },
        downPayment() { this.changes++; },
        loanTerm() { this.changes++; },
        interestRate() { this.changes++; },
        
        // resultsAnimationState(v) {
        //     switch (v) {
        //         case 0: console.log('show loading'); break;
        //         case 1: console.log('hide loading'); break;
        //         case 2: console.log('show results'); break;
        //         case 3: console.log('hide results'); break;
        //     }
        // }
    },

    // mounted() {
    //     this.hasCalculation = true;
    // },

    computed: {
        constantLoanTermUnits() {
            let u = null;
            for (const term of this.calculator.termInterests) {
                if (u) {
                    if (u !== term.units) return null;
                } else {
                    u = term.units;
                }
            }
            return u;
        },

        constantLoanTermUnitsText() {
            switch (this.constantLoanTermUnits) {
                case 'months': return 'Months';
                case 'years': return 'Years';
                default: return '???';
            }
        },

        maxDownPayment() { return this.loanAmount - 0.01; },

        downPaymentPercentage() {
            if (this.loanAmount > 0 && this.downPayment > 0) {
                // #TODO: not sure what I want the precision to be here just yet.
                return parseFloat((this.downPayment / this.loanAmount * 100).toFixed(0)) + '%';
            } else {
                return '0%';
            }
        },

        loanAmountInterval() {
            return 1;
        },

        downPaymentInterval() {
            return 1;
        },
        
        buttonTermInterests() {
            if (this.calculator.termInterests.length <= MAX_LOAN_TERMS_ON_LINE) {
                return this.calculator.termInterests;
            } else {
                return this.calculator.termInterests.slice(0, MAX_LOAN_TERMS_ON_LINE - 1);
            }
        },

        rawDropdownTermInterests() {
            if (this.calculator.termInterests.length > MAX_LOAN_TERMS_ON_LINE) {
                return this.calculator.termInterests.slice(MAX_LOAN_TERMS_ON_LINE - 1);
            } else {
                return [];
            }
        },

        // These are mapped for the dropdown.
        dropdownTermInterests() {
            return this.rawDropdownTermInterests.map((t) => ({"label": t.len, "value": t.rate}));
        },

        ...mapGetters(['monthlyPaymentForcedNumber', 'typeDisplayText']),
        ...mapState(['branding', 'calculator', 'calculatorInfoLoaded'])
    },

    async created() {
        this.$store.commit('setSendingEvents', false);
        this.listenToChanges = false;

        this.calculatorBackURL = getOriginalQueryProp('cbackbutton');
    },

    mounted() {
        this.loanAmount = this.calculator.defaultLoanAmount;
        this.downPayment = this.calculator.defaultDownPayment;
        if (Array.isArray(this.calculator.termInterests) && this.calculator.termInterests.length > 0) {
            this.loanTerm = this.calculator.termInterests[0].len;
            this.loanTermUnits = this.calculator.termInterests[0].units;
            this.interestRate = this.calculator.termInterests[0].rate;
        } else {
            this.loanTerm = 0;
            this.loanTermUnits = 'months';
            this.interestRate = 0;
        }

        setTimeout(() => {
            this.listenToChanges = true;
            this.computeChartData();
        }, 0)
    },

    methods: {
        navigateBackToList() {
            if (this.calculatorBackURL && this.calculatorBackURL.trim().length > 0) {
                window.location.href = this.calculatorBackURL;
            }
        },

        async showCalculation() {
            if (this.changes > 0) {
                this.changes = 0;
                this.$store.commit('refreshEngagement');
            }

            this.disableCalcButton = true;
            this.$store.commit('setSendingEvents', true);
            const FADEDELAY = 250;
            let doScrolling = true;

            if (this.resultsAnimationState == 2) {
                doScrolling = false;
                this.scrollToResults();
                this.resultsAnimationState = 3;
                await delay(FADEDELAY);
            }

            this.computeChartData();
            this.hasCalculation = true;
            
            this.resultsAnimationState = 0;
            await delay(FADEDELAY);
            
            if (doScrolling) {
                this.scrollToResults();
            }

            this.resultsAnimationState = 1;
            await delay(FADEDELAY);
            this.resultsAnimationState = 2;
            await delay(FADEDELAY);
            this.disableCalcButton = false;
        },

        scrollToResults() {
            if (window.outerHeight < 780) {
                window.scrollTo(0, window.outerHeight);
            }
        },

        applyForLoan() {
            this.$emit('apply', {
                loanAmount: forceNumber(this.loanAmount),
                downPayment: forceNumber(this.downPayment),
                interestRate: forceNumber(this.interestRate),
                termLength: forceNumber(this.loanTerm),
                termUnits: this.loanTermUnits
            });
        },

        // debounced
        computeChartData() {
            this.chartDataUpdateDebouncer.debounce(() => {
                this.__computeChartData();
            });
        },

        async __computeChartData() {
            await this.calculateMonthlyPayment({
                loanAmount: forceNumber(this.loanAmount),
                downPayment: forceNumber(this.downPayment),
                interestRate: forceNumber(this.interestRate),
                termLength: forceNumber(this.loanTerm),
                termUnits: this.loanTermUnits
            });
            this.chartData = [];

            if (this.calculator.visPrincipalVisible) {
                this.chartData.push({
                    label: 'Principal',
                    value: forceNumber(this.calculator.monthlyPaymentPrincipal),
                    color: this.branding.visPrincipalColor
                });
            }

            if (this.calculator.visInterestVisible) {
                this.chartData.push({
                    label: 'Interest',
                    value: forceNumber(this.calculator.monthlyPaymentInterest),
                    color: this.branding.visInterestColor
                });
            }
        },

        onDropdownLoanTermSelected(index) {
            if (index >= 0) {
                const term = this.rawDropdownTermInterests[index];
                this.setProvidedLoanTerm(term.len, term.rate);
            }
        },

        setProvidedLoanTerm(term, rate) {
            this.loanTerm = term;
            this.interestRate = rate;
        },

        setMoneyFilteredVal(key, value) {
            this[key] = this.moneyAsNumber(value);
        },

        ...mapActions([
            'calculateMonthlyPayment',
            'afterInitialPopoverDisplay'
        ])
    },

    components: {
        vueSlider,
        donutChart,
        chartLegend,
        modal,
        dropdown,
        BouncingBalls
    }
};
</script>

<style lang="scss" scoped>
@import '~@/assets/sass/variables';
@import '~@/assets/sass/mixins';

.calc-container{
    display: flex;
    flex-direction: column;
}

.branding {
    width: 100%;
    border-bottom: 1px solid $border-color;
    background-color: darken($bg-color, 1%);
    padding: 16px;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    min-height: 80px;
}

.branding-logo-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.branding-logo {
    max-width: 100%;
}

.calc-form {
    width: 100%;
}

.no-border-right {
    border-right: none;
    padding-right: 0;
}

.no-border-left {
    border-left: none;
    padding-left: 0;
}

.calc-init-popover {
    margin-top: 16px;
    .card {
        @include shadow-8dp();
    }
}

.calc-donut-chart-container {
    min-height: 256px;
    min-width: 256px;
    max-height: 320px;
    max-width: 320px;
    display: flex;
    flex-direction: row;
    align-content: stretch;
    align-items: stretch;
    justify-content: center;
}

.extra-margin-bottom { margin-bottom: $layout-padding / 2;  }
.extra-margin-top { margin-top: $layout-padding / 2;  }

.extra-padding-top { padding-top: $layout-padding; }
.extra-padding-bottom { padding-bottom: $layout-padding; }

.apply-now-btn {
    font-size: 1.8rem;
    line-height: 1.8rem;
    min-height: px2rem(64);
    padding-top: px2rem(12);
    padding-bottom: px2rem(12);
}

.branding-badge {
    padding: 8px;
    font-weight: bold;
}

@media screen and (max-width: $screen-size-sm ) {
    .calc-dchart-container {
        max-width: 200px;
    }
}

.visible-fadein {
    opacity: 1.0;
    animation: canim-fade-in 0.2s ease-in;
}

.invisible-fadeout {
    opacity: 0.0;
    animation: canim-fade-out 0.2s ease-in;
}

.invisible {
    opacity: 0.0;
    visibility: hidden;
}

.cresults-visible {
    display: inherit;
    opacity: 1.0;
    animation: canim-fade-in 0.4s ease-in;
}

.cresults-hidden {
    display: none;
    opacity: 0.0;
}

@keyframes canim-fade-in {
    from { opacity: 0; }
    to { opacity: 1.0; }
}

@keyframes canim-fade-out {
    from { opacity: 1.0; }
    to { opacity: 0; }
}

.calculations-loading {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%)
}

.noopacity {
    opacity: 0;
}

.v-icon {
    /* Flow with text content */
    display: inline-block;
    /* Inherit the parent text color */
    fill: currentColor;
    stroke: currentColor;
    /* Use the parent font-size for width and height */
    height: 1em;
    width: 1em;
    /* Vertically align icon with adjacent text */
    vertical-align: middle;
    /* Align more nicely with capital letters */
    position: relative;
    top: -0.0625em;
}

.v-icon-inner-use {
    fill: currentColor;
    stroke: currentColor;
}
</style>


