const FACEBOOK_APP_ID = process.env.FACEBOOK_APP_ID;

const GOOGLE_CLIENT_ID = '847090349006-gbp18t7gfp7vp4tercd7ca3tedi3ck9n.apps.googleusercontent.com';
const GOOGLE_CLIENT_SECRET = 'di5FbelnnWodS7nDsi3K31QH';

let __my_loaded_gapi = null;
function loadGoogleApi() {
    if (__my_loaded_gapi) { return Promise.resolve(__my_loaded_gapi); }
    return new Promise((resolve, reject) => {
        gapi.load('client', () => {
            __my_loaded_gapi = gapi;
            resolve(gapi);
        });
    });
}

export async function googleLogin() {
    await loadGoogleApi();
    await gapi.client.init({
        'clientId': GOOGLE_CLIENT_ID,
        'scope': 'email profile'
    });

    try {
        const response = await gapi.client.people.people.get({
            'resourceName': 'people/me',
            'requestMask.includeField': 'person.names'
        });
        console.log('login response: ', response);
    } catch(e) {
        console.erro('FAILED TO LOGIN: ', e);
    }
}

export function facebookGetUserInfo() {
    return new Promise((resolve, reject) => {
        try {
            FB.api('/me', 'GET', {
                "fields":"id,first_name,last_name,name,email"
            }, (response) => {
                if (response && response['error']) {
                    reject(response['error']);
                } else {
                    resolve(response);
                }
            });
        } catch(e) {
            reject(e);
        }
    });
}

export function facebookLogin() {
    return new Promise((resolve, reject) => {
        try {
            FB.getLoginStatus(function(response) {
                if (response && response['error']) {
                    reject(response['error']);
                } else if (response.status === 'connected') {
                    resolve({
                        response
                    });
                } else {
                    FB.login((response) => {
                        if (response && response['error']) {
                            reject(response['error']);
                        } else {
                            resolve({
                                response
                            });
                        }
                    }, {scope: 'email,public_profile'});
                }
            });
        } catch(e) {
            reject(e);
        }
    });
}

/**
 * Initializes the facebook api.
 */
export function facebookInit() {
    window.fbAsyncInit = function() {
        FB.init({
            appId            : FACEBOOK_APP_ID,
            autoLogAppEvents : true,
            xfbml            : true,
            version          : 'v2.9'
        });
        FB.AppEvents.logPageView();
    };

    (function(d, s, id){
        var js, fjs = d.getElementsByTagName(s)[0];
        if (d.getElementById(id)) {return;}
        js = d.createElement(s); js.id = id;
        js.src = "//connect.facebook.net/en_US/sdk.js";
        fjs.parentNode.insertBefore(js, fjs);
    }(document, 'script', 'facebook-jssdk'));
}

export function initSocialMediaPlatforms() {
    facebookInit();
}


