import {
    Selection,
    select
} from "d3-selection";

import {
    Arc,
    DefaultArcObject,
    arc,
    pie,
    Pie
} from "d3-shape";

import {
    scaleOrdinal,
    ScaleOrdinal,
    schemeCategory20c
} from "d3-scale";

import {
    interpolate
} from "d3-interpolate";

import "d3-transition";

const TAU = Math.PI * 2;

const DEFAULT_COLORS = scaleOrdinal(schemeCategory20c);

export default class DonutChart {
    /**
     * @param {number} width Viewport width for this donut chart.
     * @param {number} height Viewport height for this donut chart.
     */
    constructor(element, options) {
        this.options = Object.assign({
            width: 320,
            height: 320,
            animationDur: 500
        }, options);

        this.svg = select(element).append('svg');
        this.svg
            .attr('preserveAspectRatio', 'xMinYMin meet')
            .attr('viewBox', `0 0 ${this.options.width} ${this.options.height}`);
        this.data = [];
        this.init();
    }

    /**
     * Initializes the SVG elements for this donut chart.
     */
    init() {
        this.svg.append('g').attr('class', 'slices');
        this.radius = Math.min(this.options.width, this.options.height) / 2;
        this.pie = pie().sort(null).value((d) => d.value || 0);
        this.arc = arc().outerRadius(this.radius * 0.9).innerRadius(this.radius * 0.7);
        this.outerArc = arc().outerRadius(this.radius * 0.9).innerRadius(this.radius * 0.9);

        this.svg.append('text').attr('class', 'donut-main-text')
            .attr('x', this.options.width / 2)
            .attr('y', this.options.height / 2)
            .style('text-anchor', 'middle')
            .text('');
        
        this.svg.append('text').attr('class', 'donut-sub-text')
            .attr('x', this.options.width / 2)
            .attr('y', this.options.height / 2 + 56)
            // #TODO: dy does not work on iOS.
            // .attr('dy', '2.0rem')
            .style('text-anchor', 'middle')
            .text('');
    }

    setMainText(text) {
        // #TODO: store the element and used a variable
        this.svg.select('.donut-main-text').text(text);
    }

    setSubText(text) {
        // #TODO: store the element and used a variable
        this.svg.select('.donut-sub-text').text(text);
    }

    update(data) {
        if (data !== undefined) {
            this.data = data || [];
        }
        this.svg = this.svg.data([this.data]);

        const slices = this.svg.select('.slices')
            .attr('transform', `translate(${this.options.width / 2}, ${this.options.height / 2})`)
            .selectAll('path.slice')
            .data(this.pie(this.data), (d) => d.data.label);
        
        slices.enter().insert('path')
            .attr('class', 'slice')
            .attr('d', this.arc)
            .attr('fill', function(d, i) { return d.data.color || DEFAULT_COLORS(i); } );
        
        const _arc = this.arc;
        slices.transition().duration(this.options.animationDur)
            .attrTween('d', function(d) {
                this._current = this._current || d;
                let _interpolate = interpolate(this._current, d);
                this._current = _interpolate(0);
                return function(t) { return _arc(_interpolate(t)) }
            });
    }
}


