<template>
    <div class="bb-container">
        <span class="bb-ball bb-ball-1">&nbsp;</span>
        <span class="bb-ball bb-ball-2">&nbsp;</span>
        <span class="bb-ball bb-ball-3">&nbsp;</span>
    </div>
</template>

<script>
export default {

}
</script>

<style lang="scss" scoped>
$ball-size: 16px;
$ball-color: rgba(200, 200, 200, 0.6);
$ball-spacing: 8px;
$ball-jump-height: 6px;
$ball-anim-duration: 1s;

.bb-container {
    display: flex;
    flex-direction: row;

    margin-top: $ball-jump-height;

    .bb-ball {
        width: $ball-size;
        height: $ball-size;
        min-width: $ball-size;
        min-height: $ball-size;
        max-width: $ball-size;
        max-height: $ball-size;
        background-color: $ball-color;
        border-radius: $ball-size;

        &:not(:last-child) {
            margin-right: $ball-spacing;
        }

        animation: ball-animation $ball-anim-duration ease-in-out 0s infinite both;
    }

    .bb-ball-1 { animation-delay: 0s; }
    .bb-ball-2 { animation-delay: $ball-anim-duration / 3; }
    .bb-ball-3 { animation-delay: $ball-anim-duration / 2; }
}

@keyframes ball-animation {
    0% {
        transform: translateY(-1 * $ball-jump-height);
    }

    50% {
        transform: translateY(0px);
    }

    100% {
        transform: translateY(-1 * $ball-jump-height);
    }
}
</style>



