import http from './http';

const api = http.api(process.env.SERVER_ORIGIN);

/**
 * APIs for interacting with the server.
 */

let __CACHED_CALC_INFO = undefined;
let __CALCULATOR_SHORT_ID = null;

function getInjectedCalculatorInfo() {
    const injected = window['__ijcinfo__'];
    if (injected) {
        const injectedBrandingInfo = injected['branding'];
        const injectedCalculatorInfo = injected['calculator'];
        __CALCULATOR_SHORT_ID = injected['calculatorShortId'];
        return  {
            calculator: injectedCalculatorInfo,
            branding: injectedBrandingInfo,
            preview: !!injected['preview']
        }
    } else {
        return null;
    }
}

/**
 * Get information about the current calculator if possible.
 * Either uses information that has been injected into the html
 * if it exists, or loads information from the server.
 */
export async function getCurrentCalculatorInfo() {
    if (__CACHED_CALC_INFO === undefined) {
        const injected = getInjectedCalculatorInfo();

        if (!injected) {
            // #TODO: load information from server, if injected is null.
            console.warn('No injected calculator information found!');
        } else {
            __CACHED_CALC_INFO = injected;
        }
    }
    return __CACHED_CALC_INFO;
}

export async function reportEngagement(payload) {
    if (__CALCULATOR_SHORT_ID) {
        await api.post('/calcmetric/engage').data({
            payload: JSON.stringify(payload),
            calculatorShortId: __CALCULATOR_SHORT_ID
        }).send();
    }
}

export async function reportLead(payload) {
    if (__CALCULATOR_SHORT_ID) {
        await api.post('/calcmetric/lead').data({
            payload: JSON.stringify(payload),
            calculatorShortId: __CALCULATOR_SHORT_ID
        }).send();
    }
}


