<template>
    <div ref="donutChartContainer" class="donut-chart-container">
    </div>
</template>

<script>
import DonutChart from '../misc/donut-chart';

export default {
    name: 'DonutChart',

    data: () => ({
        dchart: null
    }),

    props: {
        mainText: { type: String },
        subText: { type: String },
        chartData: { type: Array }
    },


    watch: {
        chartData(newValue, oldValue) {
            this.dchart.update(newValue);
        },

        mainText(newValue) {
            this.dchart.setMainText(newValue);
        },

        subText(newValue) {
            this.dchart.setSubText(newValue);
        }
    },

    mounted() {
        this.dchart = new DonutChart(this.$refs.donutChartContainer, {
            width: 480,
            height: 480,
            animationDur: 450
        });

        this.dchart.setMainText(this.mainText);
        this.dchart.setSubText(this.subText);
        this.dchart.update([]);
        this.dchart.update(); // this is necessary for some reason. (gets rid of the first hiccup.)

        // const _r = (min, max) => { return min + (Math.random() * (max - min)); };
        // setInterval(() => {
        //     this.dchart.update([
        //         {'label': 'Principal', 'value': _r(10, 100), 'color': '#ff0000'},
        //         {'label': 'Interest', 'value': _r(10, 100), 'color': '#00ff00'},
        //         {'label': 'Taxes', 'value': _r(10, 100), 'color': '#0000ff'}
        //     ]);
        // }, 500);
    }
};
</script>

<style lang="scss">
// ^ THIS CANNOT BE SCOPED!!!

.donut-chart-container {
    flex: 1;
    svg {
        .donut-main-text {
            font-size: 4.0rem;
        }

        .donut-sub-text {
            font-size: 3.0rem;
        }
    }
}
</style>


