export let originalWindowQuery;
export function saveOriginalWindowQuery() {
    originalWindowQuery = parseQueryString(window.location.search, true);
    return originalWindowQuery;
}

export function getOriginalQueryProp(prop, _default) {
    if (originalWindowQuery) {
        if (originalWindowQuery.hasOwnProperty(prop)) {
            return originalWindowQuery[prop];
        }
    }
    return _default;
}

export function delay(ms) {
    return new Promise((resolve, reject) => {
        try {
            setTimeout(resolve, ms);
        } catch(e) {
            reject(e);
        }
    });
}

export function copyPropertyList(propList, src, dest) {
    for (let key of propList) {
        if (src.hasOwnProperty(key)) { dest[key] = src[key]; }
    }
    return dest;
}

export function copyExistingPropertyList(src, dest) {
    for (let key in src) {
        if (dest.hasOwnProperty(key)) { dest[key] = src[key]; }
    }
    return dest;
}

export function isStringWithContent(str) {
    return typeof str === 'string' && str.trim().length > 0;
}

const QUERY_STRING_REGEX = /([^=&]+)(?:=([^=&]+))?\&?/g;
export function parseQueryString(qstring, undefval) {
    if (qstring[0] === '?') { qstring = qstring.substr(1); }
    QUERY_STRING_REGEX.lastIndex = 0;
    let matches;
    let out = {};
    while (matches = QUERY_STRING_REGEX.exec(qstring)) {
        // #TODO: not sure if the key is allowed to have spaces.
        const queryKey = matches[1];
        const queryValue = matches[2] ? decodeURIComponent(matches[2]) : undefval;

        if (out.hasOwnProperty(queryKey)) {
            if (!Array.isArray(out[queryKey])) {
                const arr = [out[queryKey]];
                out[queryKey] = arr;
            }
            out[queryKey].push(queryValue);
        } else {
            out[queryKey] = queryValue;
        }
    }

    return out;
}

export function parseFullName(name) {
    if (name) {
        const names = name.split(' ');
        const first = names.length > 0 ? names[0] : '';
        const last = names.length > 1 ? names[names.length - 1] : '';
        return {first, last};
    } else {
        return { first: '', last: '' };
    }
}


